<script>
import careModeMixin from '@/mixins/careMode.js'

export default {
  mixins: [careModeMixin],
  
  data() {
    return {
      statusBarHeight: 0,
      isLoggedIn: false,
      userInfo: {},
      selectedAvatarSize: 160,
      showModal: false
    }
  },
  mounted() {
    this.getStatusBarHeight();
    this.checkLoginStatus();
  },
  onLoad() {
    this.checkLoginStatus();
    this.loadAvatarSize();
    this.loadCareMode();
  },
  onShow() {
    this.checkLoginStatus();
    this.loadAvatarSize();
    this.loadCareMode(); // Á°Æ‰øù‰ªéËÆæÁΩÆÈ°µÈù¢ËøîÂõûÊó∂ÈáçÊñ∞Âä†ËΩΩÂÖ≥ÊÄÄÊ®°ÂºèÁä∂ÊÄÅ
  },
  methods: {
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    checkLoginStatus() {
      const userInfo = uni.getStorageSync('userInfo');
      const isLoggedIn = uni.getStorageSync('isLoggedIn');
      
      this.isLoggedIn = isLoggedIn === true || isLoggedIn === 'true' || isLoggedIn === 1 || isLoggedIn === '1';
      this.userInfo = userInfo || {};
      
      // Ë∞ÉËØï‰ø°ÊÅØ
      console.log('Áî®Êà∑‰ø°ÊÅØ:', this.userInfo);
      console.log('Áî®Êà∑Á±ªÂûã:', this.userInfo.userType);
    },
    
    // Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢
    goToLogin() {
      uni.reLaunch({
        url: '/pages/login/login'
      });
    },
    
    // ÁºñËæëËµÑÊñô
    editProfile() {
      uni.navigateTo({
        url: '/pages/profile/edit-profile'
      });
    },
    
    // Ëé∑ÂèñÊòæÁ§∫ÂêçÁß∞
    getDisplayName() {
      // ‰ºòÂÖàÊòæÁ§∫ÊòµÁß∞
      if (this.userInfo.nickName && this.userInfo.nickName.trim()) {
        return this.userInfo.nickName.trim();
      }
      // ÂÖ∂Ê¨°ÊòæÁ§∫ÁúüÂÆûÂßìÂêç
      if (this.userInfo.name && this.userInfo.name.trim()) {
        return this.userInfo.name.trim();
      }
      // Â¶ÇÊûúÊúâÊâãÊú∫Âè∑ÔºåÊòæÁ§∫ÊòüÂè∑ÈÅÆÊå°ÁöÑÊâãÊú∫Âè∑
      if (this.userInfo.phone && this.userInfo.phone.trim()) {
        return this.maskPhoneNumber(this.userInfo.phone);
      }
      // ÈªòËÆ§ÊèêÁ§∫
      return 'ÁÇπÂáªËÆæÁΩÆÊòµÁß∞';
    },
    
    // ÊâãÊú∫Âè∑ÊòüÂè∑ÈÅÆÊå°
    maskPhoneNumber(phone) {
      if (!phone || phone.length < 11) {
        return phone;
      }
      // ‰øùÁïôÂâç3‰ΩçÂíåÂêé4‰ΩçÔºå‰∏≠Èó¥Áî®ÊòüÂè∑ÈÅÆÊå°
      return phone.substring(0, 3) + '****' + phone.substring(7);
    },
    
    // Ëé∑ÂèñÂ§¥ÂÉèÊñáÂ≠ó
    getAvatarText() {
      if (this.userInfo.nickName) {
        return this.userInfo.nickName.charAt(0).toUpperCase();
      } else if (this.userInfo.name) {
        return this.userInfo.name.charAt(0).toUpperCase();
      }
      return 'üë§';
    },
    
    // Â§ÑÁêÜÂ§¥ÂÉèÂä†ËΩΩÈîôËØØ
    handleAvatarError() {
      console.log('Â§¥ÂÉèÂä†ËΩΩÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§Â§¥ÂÉè');
      this.userInfo.avatarUrl = '';
    },
    
    
    // Âä†ËΩΩÂ§¥ÂÉèÂ§ßÂ∞èËÆæÁΩÆ
    loadAvatarSize() {
      const savedSize = uni.getStorageSync('avatarSize');
      if (savedSize) {
        this.selectedAvatarSize = savedSize;
      }
    },
    
    // Ëé∑ÂèñÁä∂ÊÄÅÊ†èÈ´òÂ∫¶
    getStatusBarHeight() {
      const systemInfo = uni.getSystemInfoSync();
      this.statusBarHeight = systemInfo.statusBarHeight || 0;
    },
    
    // Â§ÑÁêÜÂÆ¢ÊúçËÅîÁ≥ª
    // ÊòæÁ§∫ÂÆ¢ÊúçËÅîÁ≥ªÂºπÁ™ó
    showContactModal() {
      this.showModal = true;
    },
    
    // ÈöêËóèÂÆ¢ÊúçËÅîÁ≥ªÂºπÁ™ó
    hideContactModal() {
      this.showModal = false;
    },
    
    // Â§ÑÁêÜÂæÆ‰ø°ÂÆ¢ÊúçËÅîÁ≥ª
    handleContact(e) {
      console.log('ÂÆ¢ÊúçËÅîÁ≥ª‰∫ã‰ª∂:', e.detail);
      console.log('ÂÆ¢ÊúçË∑ØÂæÑ:', e.detail.path);
      console.log('ÂÆ¢ÊúçÊü•ËØ¢ÂèÇÊï∞:', e.detail.query);
      
      // ÂÖ≥Èó≠ÂºπÁ™ó
      this.hideContactModal();
      
      // ÊòæÁ§∫ËΩ¨Êé•ÊèêÁ§∫
      uni.showToast({
        title: 'Ê≠£Âú®‰∏∫ÊÇ®ËΩ¨Êé•ÂÆ¢Êúç',
        icon: 'loading',
        duration: 1500
      });
    },
    
    // Â§çÂà∂ÂæÆ‰ø°Âè∑
    copyWechat() {
      const wechatId = 'lixiaobao_service'; // ÊõøÊç¢‰∏∫ÂÆûÈôÖÁöÑÂæÆ‰ø°Âè∑
      uni.setClipboardData({
        data: wechatId,
        success: () => {
          this.hideContactModal();
          uni.showToast({
            title: 'ÂæÆ‰ø°Âè∑Â∑≤Â§çÂà∂',
            icon: 'success',
            duration: 2000
          });
        },
        fail: () => {
          uni.showToast({
            title: 'Â§çÂà∂Â§±Ë¥•',
            icon: 'none',
            duration: 2000
          });
        }
      });
    },
    
    // Ë∑≥ËΩ¨Âà∞ËÆæÁΩÆÈ°µÈù¢
    goToSettings() {
      uni.navigateTo({
        url: '/pages/settings/settings'
      });
    },
    
    // Ë∑≥ËΩ¨Âà∞‰∏™‰∫∫ËµÑÊñôÁºñËæëÈ°µÈù¢
    goToProfile() {
      uni.navigateTo({
        url: '/pages/profile/edit-profile'
      });
    },
    
    
  }
}
</script>

